<template>
  <div>
    <section class="content-header">
      <h1>Quotes</h1>
    </section>
    <section class="content vld-parent" ref="ref_load_filter_quotes">
      <div class="card box-warning">
        <div class="box-header with-border">
          <h3 class="box-title card-text">Quotation Search</h3>
        </div>
        <div class="box-body">

          <form @submit.prevent="loadQuotes()" class="row">
            <div class="col-xs-12 col-sm-12 col-md-2 col-lg-2">
              <div class="form-group">
                <label>Yard Location</label>
                <select class="form-control" v-model="search.yard_location">
                  <option>select</option>
                  <option>A</option>
                  <option>B</option>
                  <option>C</option>
                  <option>D</option>
                </select>
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label>Customer</label>
                <input type="text" class="form-control" placeholder="Enter Customer..." v-model="search.name" />
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-2 col-lg-2">
              <div class="form-group calendar-icon">
                <label>Date</label>
                <input type="date" class="form-control" v-model="search.date" />
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-2 col-lg-2">
              <div class="form-group">
                <label>Status</label>
                <select class="form-control" v-model="search.status">
                  <option value="Submitted">Submitted</option>
                  <option value="Pending">Pending</option>
                  <option value="Approved">Approved</option>
                  <option value="Declined">Declined</option>
                </select>
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-2 col-lg-2">
              <div class="form-group">
                <label style="display: block">
                  &nbsp;&nbsp;
                </label>
                <button type="submit" class="btn link1">
                  Filter
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
      <div class="card box-black">
        <div class="box-header with-border">
          <h3 class="box-title card-text">Tasks List</h3>
        </div>
        <div class="bottom-box-header">
          <div class="pull-left">
            <div class="Showingbox">
              <span class="pull-left">Showing </span>
              <div class="pull-left drop">
                <select class="form-control" v-model="ppp">
                  <option value="25">25</option>
                  <option value="50">50</option>
                  <option value="100">100</option>
                </select>
              </div>
              <span class="pull-left">entries </span>
              <div class="clearfix"></div>
            </div>
          </div>
          <div class="pull-right">
            <router-link to="/quotes/add" class="btn link1">
              <span><i class="fa fa-plus-square-o" aria-hidden="true"></i>
              </span>
              Get a Quote
            </router-link>
          </div>
          <div class="clearfix"></div>
        </div>
        <div class="box-body">
          <div class="tablesection">
            <div class="table-responsive">
              <table class="table table-striped table-hover">
                <thead>
                  <tr>
                    <th style="text-align: center" width="70">
                      Sl No.
                    </th>
                    <th>Customer</th>
                    <th>Yard Location</th>
                    <th>Date</th>
                    <th>Volume</th>
                    <th>Pending With</th>
                    <th width="100">Status</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(ua, x) in ulist.data" :key="ua.id">
                    <td align="center">{{ x + 1 }}</td>
                    <td>
                      <router-link :to="'/quotes/edit/' + ua.id" class="text-green">{{ ua.name }}</router-link>
                    </td>
                    <td></td>
                    <td>{{ ua.created_at | myDate }}</td>
                    <td></td>
                    <td>{{ ua.pending_with }}</td>
                    <td>
                      <span class="badge" :class="
                        ua.status == 'Approved' ? 'bg-green' : ua.status == 'Pending' ? 'bg-yellow' : 'bg-red'
                      ">{{ ua.status }}</span>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div class="paginationsectionbox">
            <div class="row">
              <div class="col-xs-12 col-sm-12 col-md-5 col-lg-5">
                <div class="Showingboxto1">
                  Showing {{ ulist.from }} to
                  {{ ulist.to }} of {{ ulist.total }} results
                </div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-7 col-lg-7">
                <div class="Showingboxto2">
                  <ul>
                    <li v-for="(
                                                links, index
                                            ) in ulist.links" :key="index" :class="{
                                              'active disabled': links.active,
                                              disabled: !links.url,
                                              paginate_button:
                                                index != 0 &&
                                                index + 1 !=
                                                ulist.links.length,
                                            }">
                      <a href="javascript:void(0);" v-html="links.label" @click="loadQuotes(links.url)"></a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: "userlist",
  data() {
    return {
      ppp: 25,
      search: {
        organisation: "",
        type: "",
        name: "",
        phone: "",
        email: "",
      },
      editmode: false,
      ulist: "",
      users: {},
      form: new Form({
        id: "",
        type: "",
        name: "",
        email: "",
        password: "",
        email_verified_at: "",
      }),
    };
  },
  watch: {
    async ppp() {
      await this.loadQuotes();
    },
    async "search.organization"(n) {
      if (n == "United Scrap Metal") {
        if (
          ![
            "Admin",
            "Finance Team",
            "Transportation Team",
            "Sales Team",
            "CEC",
          ].includes(this.search.type)
        ) {
          this.search.type = "";
        }
      } else {
        if (
          !["Primary", "Finance", "Transportation"].includes(
            this.search.type
          )
        ) {
          this.search.type = "";
        }
      }
    },
  },
  methods: {
    // deleteUser(id) {
    //   Swal.fire({
    //     title: "Are you sure?",
    //     text: "You won't be able to revert this!",
    //     showCancelButton: true,
    //     confirmButtonColor: "#d33",
    //     cancelButtonColor: "#3085d6",
    //     confirmButtonText: "Yes, delete it!",
    //   }).then((result) => {
    //     // Send request to the server
    //     if (result.value) {
    //       this.form
    //         .delete("api/user/" + id)
    //         .then(() => {
    //           Swal.fire(
    //             "Deleted!",
    //             "Your file has been deleted.",
    //             "success"
    //           );
    //           // Fire.$emit('AfterCreate');
    //           this.loadQuotes();
    //         })
    //         .catch((data) => {
    //           Swal.fire("Failed!", data.message, "warning");
    //         });
    //     }
    //   });
    // },
    loadQuotes(url = "/api/quotes") {
      // if(this.$gate.isAdmin()){
      // axios.get("api/user").then(({ data }) => (this.users = data));
      let UFloder = this.$loading.show({
        container: this.$refs.ref_load_filter_quotes,
      });
      axios
        .get(url, {
          params: {
            yard_location: this.search.yard_location,
            name: this.search.name,
            date: this.search.date,
            status: this.search.status,
            ppp: this.ppp,
          },
        })
        .then((res) => {
          this.ulist = res.data.data;
          UFloder.hide();
        })
        .catch((err) => {
          Toast.fire({
            icon: "error",
            title: "Some error occured! Please try again",
          });
          UFloder.hide();
        });
    },
  },
  mounted() {
    console.log("User Component mounted.");
  },
  created() {
    this.$Progress.start();
    this.loadQuotes();
    this.$Progress.finish();
  },
};
</script>
